import base from '../api/base.js'
import log from './log.js'
const baseUrl = 'http://127.0.0.1:9022' //
const socketUrl = 'ws://192.163.1.1:8081' //socket

const httpRequest = (url, methods, data, type, mycontent) => {
	log.info('------ ' + url + '-----' + JSON.stringify(data))
	// console.log(url)
	let meth = methods.toUpperCase(); //
	if (!data) {
		data = {}
	}
	let contentType = meth == 'POST' ? 'application/x-www-form-urlencoded' : 'application/json'
	if (mycontent == 1) {
		contentType = 'application/json'
	}
	let httpDefaultOpts = {
		url: url,
		data: data,
		method: meth,
		header: {
			'X-Requested-With': 'XMLHttpRequest',
			'content-type': contentType,
			// 'Cookie': uni.getStorageSync('cookieKey')   //
		},
		dataType: 'json',
	}
	return new Promise(function(resolve, reject) {
		if (!type) {
			uni.request(httpDefaultOpts).then(
				(res) => {
					log.info('------ ' + url + '-----' + JSON.stringify(res))
					//	console.log(res); //
					if (res && res[1].header && res[1].header['Set-Cookie']) {
						// console.log("",res[1].header['Set-Cookie'])
						try {
							// uni.setStorageSync('cookieKey', res[1].header['Set-Cookie']); //CookieStorage
						} catch (e) {}
					} else {
					}
					if (res[1].data.code == 20000) { //
						// console.log(res[1].data.code)
						resolve(res[1].data)
					} else if (res[1].data.code == 0) {
						resolve(res[1].data)
					} else if (res[1].data.code == 1) {
						reject(res[1].data)
					} else if (res[1].data.code == 500) {
						uni.showToast({
							title: '', //
							icon: 'none'
						})
						reject(res[1].data)
					} else { //
						if (res[1].data.message != '') {
							uni.showToast({
								title: res[1].data.message ? res[1].data.message :
								'', //
								icon: 'none'
							})
						}
						reject(res[1]) // 
					}
				}
			).catch(
				(response) => {
					// console.log(response)
					log.error('-----load in ' + url + '-----' + JSON.stringify(response))
					reject(response)
					uni.showToast({
						title: ',', //
						icon: 'none'
					})
				}
			)
		} else if (type == 1) {
			uni.uploadFile({
				url: url,
				filePath: data.path,
				name: data.name,
				success: (res) => {
					res = JSON.parse(res.data)
					if (res.code == 20000) {
						resolve(res)
						log.info('------ ' + url + '-----' + JSON.stringify(res))
					} else {
						reject(res)
						log.error('-----load in ' + url + '-----' + JSON.stringify(res))
						uni.showToast({
							title: '', //
							icon: 'none'
						})
					}
				},
				fail: (err) => {
					log.error('-----load in ' + url + '-----' + JSON.stringify(err))
					reject(err)
				}
			});
		}
	})
}



export default {
	baseUrl,
	socketUrl,
	httpRequest
}
